import React from "react";
import "./Form.css"
import {Space, Radio, Form as AntdForm, Checkbox, Input,} from "antd"

type Props = { guestName: string, setForm: (_: any, form: any) => void };
export const Form = ({guestName, setForm}: Props) => {
    return <AntdForm name={guestName} onValuesChange={setForm}>
        <div className={"form"}>
            <div className={"form-name-block"}>
                <div className={"form-name-guest"}>{guestName}</div>
            </div>
            <div className={"form-content"}>
                <div className={"form-block"}>
                    <div className={"form-block-caption"}></div>
                    <AntdForm.Item name={"ready"}>
                        <Radio.Group>
                            <Space direction={"vertical"}>
                                <Radio value={"yes"}> </Radio>
                                <Radio value={"no"}>  </Radio>
                            </Space>
                        </Radio.Group>
                    </AntdForm.Item>
                </div>
                <div className={"form-block"}>
                    <div className={"form-block-caption"}> <br/>  </div>
                    <AntdForm.Item name={"alcoholDrinks"}>
                        <Checkbox.Group>
                            <Space direction={"vertical"}>
                                <Checkbox value={"white-wine"}> </Checkbox>
                                <Checkbox value={"red-wine"}> </Checkbox>
                                <Checkbox value={"cognac"}></Checkbox>
                                <Checkbox value={"whiskey"}></Checkbox>
                                <Checkbox value={"no"}>  </Checkbox>
                            </Space>
                        </Checkbox.Group>
                    </AntdForm.Item>
                    <AntdForm.Item name={"alcoholDrinks-other"}>
                        <div className={"form-other"}>
                            <div></div>
                            <div className={"other-input"}><Input bordered={false}/></div>
                        </div>
                    </AntdForm.Item>
                </div>
                <div className={"form-block"}>
                    <div className={"form-block-caption"}> <br/>  </div>
                    <AntdForm.Item name={"drinks"}>
                        <Checkbox.Group>
                            <Space direction={"vertical"}>
                                <Checkbox value={"juice"}> /</Checkbox>
                                <Checkbox value={"soda"}> </Checkbox>
                                <Checkbox value={"juiceDrink"}></Checkbox>
                                <Checkbox value={"water"}></Checkbox>
                            </Space>
                        </Checkbox.Group>
                    </AntdForm.Item>
                    <AntdForm.Item name={"drinks-other"}>
                        <div className={"form-other"}>
                            <div></div>
                            <div className={"other-input"}><Input bordered={false}/></div>
                        </div>
                    </AntdForm.Item>
                </div>
                <div className={"form-block"}>
                    <div className={"form-block-caption"}>  </div>
                    <AntdForm.Item name={"hotFood"}>
                        <Checkbox.Group>
                            <Space direction={"vertical"}>
                                <Checkbox value={"meat"}></Checkbox>
                                <Checkbox value={"chicken"}></Checkbox>
                                <Checkbox value={"fish"}></Checkbox>
                                <Checkbox value={"vegetables"}></Checkbox>
                            </Space>
                        </Checkbox.Group>
                    </AntdForm.Item>
                    <AntdForm.Item name={"hotFood-other"}>
                        <div className={"form-other"}>
                            <div></div>
                            <div className={"other-input"}><Input bordered={false}/></div>
                        </div>
                    </AntdForm.Item>
                </div>
                <div className={"form-block"}>
                    <div className={"form-block-caption comment"}></div>
                    <div className={"comment-text"}>    <br/>  </div>
                    <AntdForm.Item name={"comment"}>
                        <Input.TextArea style={{resize: "none"}} placeholder={""}/>
                    </AntdForm.Item>
                </div>
            </div>
        </div>
    </AntdForm>
}